import { CreateItemService } from './../create-item.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-table-delete-update',
  templateUrl: './table-delete-update.component.html',
  styleUrls: ['./table-delete-update.component.css']
})
export class TableDeleteUpdateComponent implements OnInit {

  items:any;
  itemNo:any;
  constructor(private service:CreateItemService) { }

  public deleteItem(idNo:number){
    let res = this.service.deleteItem(idNo);
    res.subscribe((data)=>this.items=data);
  }

  public findItemByItemNo(){
    if(this.itemNo){
      if(isNaN(this.itemNo)){
        alert('Characters are not allowed, please enter a number.')
      }else{
        let res = this.service.getItemByItemNo(this.itemNo);
        res.subscribe((data)=>this.items=data);
      }
    }else{ 
      let res = this.service.getItems();
      res.subscribe((data)=>this.items=data);
    }

  }

  public removeSearch(){
    this.itemNo = "";
    let res = this.service.getItems();
    res.subscribe((data)=>this.items=data);
  }

  ngOnInit(): void {
    let res = this.service.getItems();
    res.subscribe((data)=>this.items=data);
  }

}
