import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CreateItemService {

  constructor(private http:HttpClient) { }

  public addItem(item){
    return this.http.post("/api/addItem",item,{responseType:'text' as 'json'});
  }

  public getItems(){
    return this.http.get("/api/getListOfItems")
  }

  public getItemByItemNo(itemNo){
    return this.http.get("/api/getItem/"+itemNo)
  }

  public deleteItem(itemNo){
    return this.http.delete("/api/deleteItem/"+itemNo)
  }

  public withdrawalItem(itemNo,amount){
    return this.http.put("/api/withdrawalItem/"+itemNo+"/"+amount,null)
  }

  public depositItem(itemNo,amount){
    return this.http.put("/api/depositItem/"+itemNo+"/"+amount,null)
  }
}
