import { CreateItemService } from './../create-item.service';
import { Item } from '../item';
import { Component, OnInit } from '@angular/core';
import * as $ from "jquery";

@Component({
  selector: 'app-update-item',
  templateUrl: './update-item.component.html',
  styleUrls: ['./update-item.component.css']
})
export class UpdateItemComponent implements OnInit {
  item: Item = new Item(0, "", 0, 0);//Withdrawal
  itemD: Item = new Item(0, "", 0, 0);//Deposit


  message: any;

  constructor(private service: CreateItemService) { }

  ngOnInit(): void {
  }

  public noItemMessage(itemNo:number,type:string){
    $("#message").text("There is no item with Item no "+itemNo)
    $("#message").show().delay(2000).fadeOut();
    $("#message").css('color','red');
    if(type==="w"){
      this.item.itemNo=undefined;
      this.item.amount=undefined;
    }else{
      this.itemD.itemNo=undefined;
      this.itemD.amount=undefined;
    }
  }

  public successMessage(itemNo:number,amount:number,type:string){
    $("#message").text("Successfuly "+type+" quantity of " +
    amount + " from item no " + itemNo);
    $("#message").show().delay(2000).fadeOut();
    $("#message").css('color','green');
    if(type==="Withdrawal"){
      this.item.itemNo=undefined;
      this.item.amount=undefined;
    }else{
      this.itemD.itemNo=undefined;
      this.itemD.amount=undefined;
    }

  }

  public withdrawalNow() {
    if (this.item.itemNo && this.item.amount) {
      let resExist = this.service.getItemByItemNo(this.item.itemNo);
      resExist.subscribe((data)=>{
        if(data[0]){
          let res = this.service.withdrawalItem(this.item.itemNo, this.item.amount);
          res.subscribe();
          this.successMessage(this.item.itemNo, this.item.amount,"Withdrawal")
        }else{
          this.noItemMessage(this.item.itemNo,"w");
        }
      });
    } else {
      alert('Please fill all required fields.');
    }

  }

  public depositNow() {
    if (this.itemD.itemNo && this.itemD.amount) {
      let resExist = this.service.getItemByItemNo(this.itemD.itemNo);
      resExist.subscribe((data)=>{
        if(data[0]){
          let res = this.service.depositItem(this.itemD.itemNo, this.itemD.amount);
          res.subscribe();
          this.successMessage(this.itemD.itemNo, this.itemD.amount,"Deposit")
        }else{
          this.noItemMessage(this.itemD.itemNo,"d");
        }
      });
      
    } else {
      alert('Please fill all required fields.');
    }

  }

}
