import { CreateItemService } from './../create-item.service';
import { Item } from '../item';
import { Component, OnInit } from '@angular/core';
import * as $ from "jquery";

@Component({
  selector: 'app-create-item',
  templateUrl: './create-item.component.html',
  styleUrls: ['./create-item.component.css']
})
export class CreateItemComponent implements OnInit {

  item: Item = new Item(0,"",0,0);
  message:any;
  name:any;

  constructor(private service:CreateItemService) { }

  ngOnInit(): void {
  }
 

  public createNow(){
    if(this.item.name && this.item.amount && this.item.inventoryCode){
      let res = this.service.addItem(this.item)
      res.subscribe();
      $("#message").text("Successfuly added item "+this.item.name+" to stock.");
      $("#message").show().delay(2000).fadeOut();
      this.item.name=undefined;
      this.item.amount=undefined;
      this.item.inventoryCode=undefined;
    }else{
      alert('Please fill all required fields.');
    }
  }

}
